import React from 'react';
import 'rivet-uits/css/rivet.css'
import { Header } from 'rivet-react'
import { BrowserRouter as Router, Route } from 'react-router-dom'
import { Home, Calendar, Maintenance, Notice, Notices } from './pages'

const App = () => (
  <Router>
    <React.Fragment>
      <Header title={"Status.IU"} />
      <Route exact path={process.env.PUBLIC_URL + '/'} component={Home} />
      <Route exact path={process.env.PUBLIC_URL + '/notices'} component={Notices} />
      <Route path={process.env.PUBLIC_URL + '/notices/:noticeId'} component={Notice} />
      <Route path={process.env.PUBLIC_URL + '/calendar'} component={Calendar} />
      <Route path={process.env.PUBLIC_URL + '/maintenance'} component={Maintenance} />
    </React.Fragment>
  </Router>
)

export default App;
